import React, { Component } from 'react';

import {treeData, treeData2, mockFlatArray} from './mockData';
import {Tree, treeUtil} from '../../lib/react-d3-tree.min';

import './App.css';


class App extends Component {

  constructor() {
    super();

    this.state = {
      data: treeData,
      orientation: 'horizontal',
      pathFunc: 'diagonal',
      translateX: 200,
      translateY: 200,
      collapsible: true,
      initialDepth: undefined,
      depthFactor: undefined,
      zoomable: true,
      transitionDuration: 500
    };

    this.setTreeData = this.setTreeData.bind(this);
    this.setTreeDataFromCSV = this.setTreeDataFromCSV.bind(this);
    this.setTreeDataFromJSON = this.setTreeDataFromJSON.bind(this);
    this.setTreeDataFromFlatJSON = this.setTreeDataFromFlatJSON.bind(this);
    this.setTreeDataFromFlatArray = this.setTreeDataFromFlatArray.bind(this);
    this.setOrientation = this.setOrientation.bind(this);
    this.setPathFunc = this.setPathFunc.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.toggleCollapsible = this.toggleCollapsible.bind(this);
    this.toggleZoomable = this.toggleZoomable.bind(this);
  }

  setTreeData(data) {
    this.setState({ data });
  }

  setTreeDataFromCSV(csvFile) {
    treeUtil.parseCSV(csvFile, ["CSV Attribute A", "CSV Attribute B"])
    .then((data) => {
      console.log(data);
      this.setState({data})
    })
    .catch((err) => console.error(err));
  }

  setTreeDataFromJSON(jsonFile) {
    treeUtil.parseJSON(jsonFile)
    .then((data) => {
      console.log(data);
      this.setState({data})
    })
    .catch((err) => console.error(err));
  }

  setTreeDataFromFlatJSON(jsonFile) {
    treeUtil.parseFlatJSON(jsonFile, ["FlatJSON Attribute A", "FlatJSON Attribute B"])
    .then((data) => {
      console.log(data);
      this.setState({data})
    })
    .catch((err) => console.error(err));
  }

  setTreeDataFromFlatArray(flatArray) {
    const data = treeUtil.generateHierarchy(flatArray);
    console.log(data);
    this.setState({data});
  }

  setOrientation(orientation) {
    this.setState({ orientation });
  }

  setPathFunc(pathFunc) {
    this.setState({ pathFunc });
  }

  handleChange(evt) {
    const target = evt.target;
    this.setState({
      [target.name]: parseInt(target.value, 10),
    });
  }

  toggleCollapsible() {
    this.setState((prevState) =>
      ({ collapsible: !prevState.collapsible })
    );
  }

  toggleZoomable() {
    this.setState(prevState =>
      ({ zoomable: !prevState.zoomable })
    )
  }

  render() {
    return (
      <div className="App">
        <div>
          Data Set:
          <button onClick={() => this.setTreeData(treeData)}>
            TreeData 1
          </button>
          <button onClick={() => this.setTreeData(treeData2)}>
            TreeData 2
          </button>
          <button onClick={() => this.setTreeDataFromCSV('csv-example.csv')}>
            From CSV File
          </button>
          <button onClick={() => this.setTreeDataFromJSON('json-example.json')}>
            From JSON File
          </button>
          <button onClick={() => this.setTreeDataFromFlatJSON('flat-json-example.json')}>
            From Flat JSON File
          </button>
          <button onClick={() => this.setTreeDataFromFlatArray(mockFlatArray)}>
            From Flat Array
          </button>
        </div>

        <div>
          Orientation:
          <button onClick={() => this.setOrientation('horizontal')}>{'Horizontal'}</button>
          <button onClick={() => this.setOrientation('vertical')}>{'Vertical'}</button>
        </div>

        <div>
          Path Func:
          <button onClick={() => this.setPathFunc('diagonal')}>{'Diagonal'}</button>
          <button onClick={() => this.setPathFunc('elbow')}>{'Elbow'}</button>
        </div>

        <div>
          <button onClick={this.toggleCollapsible}>
            {`Collapsible: ${this.state.collapsible}`}
          </button>
        </div>

        <div>
          <div>
            <label htmlFor="translateX">Translate X:</label>
            <input
              name="translateX"
              type="number"
              defaultValue={this.state.translateX}
              onChange={this.handleChange}
            />
          </div>
          <div>
            <label htmlFor="translateY">Translate Y:</label>
            <input
              name="translateY"
              type="number"
              defaultValue={this.state.translateY}
              onChange={this.handleChange}
            />
          </div>
        </div>

        <div>
          <label htmlFor="initialDepth">Initial depth on render:</label>
          <input
            style={{color: 'grey'}}
            name="initialDepth"
            type="text"
            value={this.state.initialDepth || 'maximum'}
            readOnly
          />
        </div>

        <div>
          <label htmlFor="depthFactor">Depth Factor:</label>
          <input
            name="depthFactor"
            type="number"
            defaultValue={this.state.depthFactor}
            onChange={this.handleChange}
          />
        </div>

        <div>
          {`Zoomable: ${this.state.zoomable}`}
        </div>

        <div>
          <label htmlFor="transitionDuration">Transition duration:</label>
          <input
            name="transitionDuration"
            type="number"
            defaultValue={this.state.transitionDuration}
            onChange={this.handleChange}
          />
        </div>

        <div className="treeContainer">
          <Tree
            data={this.state.data}
            orientation={this.state.orientation}
            translate={{ x: this.state.translateX, y: this.state.translateY }}
            pathFunc={this.state.pathFunc}
            collapsible={this.state.collapsible}
            initialDepth={this.state.initialDepth}
            zoomable={this.state.zoomable}
            transitionDuration={this.state.transitionDuration}
            depthFactor={this.state.depthFactor}
          />
        </div>
      </div>
    );
  }
}

export default App;



